<template>
  <transition name="fade" mode="in-out">
    <div class='forumPage'>
      <v-Header class="head">
        <span slot='text'>我的发帖</span>
      </v-Header>
      <hr>
      <div id='forumListScroll'  v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <div>
          <v-forumList :forumLists='forumLists' :gotopage='gotopage' :removePost='removePost' :sendComment='sendComment'/>
          <v-loadmove v-if='forumLists.length>0' :type='loadType'/>
          <div class='forumListTips' v-if='!this.loading&&forumLists.length==0'>你还没有发送过帖子，请点击添加哦^_^</div>
        </div>
      </div>
      <div class='sendPostBtn' @click='gotopath'>我要发帖</div>
    <router-view v-on:update-comment-list="updateCommentList" :sendComment='sendComment' :needUpdate='needUpdate'/>
    <mt-popup class='addCommentpopup' :modal=false v-model="addCommentVisible" position="right">
      <v-commentpage :isforumPage=true :articleId='sendCommentId' :atUserId='sendCommentatUserId' :comeBack='Back' v-on:update-comment-list="updateCommentList"/>
    </mt-popup>
    <mt-actionsheet :actions="delCommentActions" v-model="delCommentVisible"></mt-actionsheet>
    </div>
  </transition>
</template>
<script>
import { MessageBox,Indicator } from 'mint-ui';
import Header from '../../components/header';
import commentPage from '../articleDetails/commentPage';
import forumList from '../forum/forumList';
import { mapState } from 'vuex';
import loadMove1 from '../../components/seeOcean/loadMore';
export default {
    name:'',
    props:[],
    components:{
      'v-Header':Header,
      'v-forumList':forumList,
      'v-commentpage': commentPage,
      'v-loadmove': loadMove1,
    },
    data () {
        return {
          tabList:[],
          select:0,
          lastDiscussId:0,
          scroll:'',
          forumLists:[],
          addCommentVisible:false,
          sendCommentId:0,
          sendCommentatUserId:0,
          delCommentActions: [{
            name: '删除评论',
            method: this.delComment
          }],
          delCommentVisible:false,
          delCommentInfo:{},
          needUpdate:1,
          isUpdate:false,
          loadType:'more',
          loading:true,
        }
    },
    watch:{
      delCommentVisible(v,o){
        if(!v){
          this.delCommentInfo = {};
        }
      }
    },
    computed:{
      ...mapState({
        _userid:state => state.userInfo.userId,//当前用户id
      }),
    },
    methods:{
       gotopath(){
        this.$router.push({path:`/post/part`});
      },
      gotopage(id){
        this.$router.push(`/personalHomePage/forumPage/forumDetailsPage/${id}`);
      },
      loadMore(){
        this.getDiscussList(this.select);
      },
      initPage(){
        this.lastDiscussId = 0;
        this.getDiscussList(this.select);
      },
      getDiscussList(id){
        if(this.loadType == 'all'||this.loading) return;
        this.loading = true;
        this.$http.get(`${this.$port.MY_DISCUSS_PROT}?lastDiscussId=${this.lastDiscussId}`,{
				}).then((response) => {
          this.loading=false;
          if (this.select==id){
            const {data} = response;
            if(response.code == 200){
              if(data.length==0){
                this.loadType = 'all';
                return
              }
              if(this.isUpdate||this.lastDiscussId==0){             
                this.isUpdate = false;
                this.forumLists = [];
              }
              this.forumLists = [...this.forumLists,...data];
              this.lastDiscussId = data.pop().discussId;
              console.log(this.forumLists)
            }
					}
				})
				.catch((error) => {
          this.loading=false;
					console.log(error.response.data);
				});
      },
      removePost(discussId){
        event.stopPropagation();
        MessageBox({
          title: '',
          message: '确定删除吗?',
          showCancelButton: true,
          confirmButtonText:'删除'
        }).then(action => {
          if(action==='confirm'){
            Indicator.open({
              text: '正在删除...',
            });
            this.$http.post(`${this.$port.DISCUSS_DEL_PROT}?discussId=${discussId}`,{
            }).then((response) => {
              const {data} = response;
              if(response.code == 200){
                Indicator.close();
                this.isUpdate = true;
                this.initPage();
              }
            })
            .catch((error) => {
              console.log(error.response.data);
            });
          }
        });
      },
      sendComment(discussId,atUserId=0,commentId=0){
        if(atUserId==this._userid){
          this.delCommentInfo.id = commentId;
          this.delCommentVisible=true;
        }else{
          this.sendCommentId=discussId;
          this.sendCommentatUserId=atUserId;
          this.addCommentVisible = true;
        }
      },
      Back(){
         this.addCommentVisible = false;
      },
      updateCommentList(){
        this.isUpdate = true;
        this.loading=false;
        this.loadType = 'more';
        this.initPage();
      },
      delComment(id){
        const _url = `${this.$port.COMMENT_DEL}?commentId=${this.delCommentInfo.id}`;
        this.$http.post(`${_url}`,{
        }).then((response) => {
          const {data} = response;
          if(response.code == 200){
           if(!this.$route.params.discussId){
              this.updateCommentList();
            }
            this.needUpdate++;
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
			},
    },
    created() {
      this.isUpdate = true;
      this.loading=false;
      this.loadType = 'more';
      this.initPage();
    },
}
</script>

<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
.forumPage{
  position: absolute;
  top: 0;
  left: 0;
  width:100%;
  height:100%;
  overflow:hidden;
  background:white;
  .forumType{
    width:6.9rem;
    height:0.87rem;
    margin:0 0.3rem;
    font-size:0.32rem;
    line-height:0.87rem;
    color:#666666;
    li{
      float:left;
      width:25%;
      font-weight: bold;
      &.active{
        color:#1797c6;
      }
    }
  }
  #forumListScroll{
    position: absolute;
    top: 0.82rem;
    left: 0;
    right:0;
    bottom:0.88rem;
    margin:auto;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
  hr{
    width:100%;
    height: 0.02rem;
    background: #f2f2f2;
    }
  .sendPostBtn{
    position:fixed;
    bottom:0;
    left:0;
    height:0.88rem;
    width:100%;
    line-height:0.88rem;
    text-align:center;
    background:url('../../assets/images/publishBtn.jpg') no-repeat;
    background-size:cover;
    color:white;
    font-size:0.36rem;
    font-weight: bold;
  }
  .forumListTips{
    text-align:center;
    font-size:0.36rem;
    font-weight: bold;
  }
  .addCommentpopup{
      width: 100%;
      height:100%;
      background:rgba(0,0,0,0);
    }
}
</style>




